"use client";

import React, { useState, useEffect } from 'react';
import { Search, MapPin, Star, Calendar, ChevronLeft, ChevronRight, Filter, Clock, Award, Users } from 'lucide-react';
import doctorsData from '../data.json';

// Import shadcn components
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogFooter,
} from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { Alert, AlertDescription } from "@/components/ui/alert";

// Helper functions remain the same
const isAvailableToday = (availabilityArray) => {
  if (!availabilityArray || !Array.isArray(availabilityArray) || availabilityArray.length === 0) {
    return false;
  }
  
  const days = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"];
  const today = days[new Date().getDay()];
  
  return availabilityArray.some(slot => slot.day === today);
};

const getAvailabilityText = (availability) => {
  if (!availability || !Array.isArray(availability) || availability.length === 0) {
    return "No availability information";
  }
  
  return availability.map(slot => `${slot.day}s: ${slot.startTime}-${slot.endTime}`).join(', ');
};

// Booking Modal now accepts an onBook prop
const BookingModal = ({ isOpen, onClose, doctor, onBook }) => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    phone: '',
    date: '',
    time: '',
    reason: ''
  });
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitMessage, setSubmitMessage] = useState('');
  const [submitStatus, setSubmitStatus] = useState('');

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prevData => ({
      ...prevData,
      [name]: value
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsSubmitting(true);

    try {
      // Prepare appointment data
      const appointmentData = {
        ...formData,
        doctorId: doctor.id,
        doctorName: doctor.name,
        appointmentId: Date.now(),
        createdAt: new Date().toISOString()
      };

      // Instead of calling a backend API, update the doctor's appointments locally
      onBook(appointmentData);
      setSubmitStatus('success');
      setSubmitMessage('Booking successful! We will contact you soon to confirm your appointment.');
      
      // Reset form after successful submission
      setFormData({
        name: '',
        email: '',
        phone: '',
        date: '',
        time: '',
        reason: ''
      });
      
      // Close modal after 3 seconds on success
      setTimeout(() => {
        onClose();
        setSubmitMessage('');
        setSubmitStatus('');
      }, 3000);
    } catch (error) {
      console.error('Booking error:', error);
      setSubmitStatus('error');
      setSubmitMessage('Failed to book appointment. Please try again later.');
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="sm:max-w-md">
        <DialogHeader>
          <DialogTitle>Book Appointment with Dr. {doctor.name}</DialogTitle>
          <DialogDescription>
            Fill in your details to schedule an appointment
          </DialogDescription>
        </DialogHeader>
        
        {submitMessage ? (
          <Alert variant={submitStatus === 'success' ? "default" : "destructive"} className="my-4">
            <AlertDescription>{submitMessage}</AlertDescription>
          </Alert>
        ) : (
          <form onSubmit={handleSubmit} className="space-y-4">
            <div className="grid w-full items-center gap-2">
              <Label htmlFor="name">Full Name</Label>
              <Input
                id="name"
                name="name"
                type="text"
                required
                placeholder="Enter your full name"
                value={formData.name}
                onChange={handleChange}
              />
            </div>
            
            <div className="grid w-full items-center gap-2">
              <Label htmlFor="email">Email Address</Label>
              <Input
                id="email"
                name="email"
                type="email"
                required
                placeholder="example@email.com"
                value={formData.email}
                onChange={handleChange}
              />
            </div>
            
            <div className="grid w-full items-center gap-2">
              <Label htmlFor="phone">Phone Number</Label>
              <Input
                id="phone"
                name="phone"
                type="tel"
                required
                placeholder="Enter your phone number"
                value={formData.phone}
                onChange={handleChange}
              />
            </div>
            
            <div className="grid grid-cols-2 gap-4">
              <div className="grid w-full items-center gap-2">
                <Label htmlFor="date">Preferred Date</Label>
                <Input
                  id="date"
                  name="date"
                  type="date"
                  required
                  min={new Date().toISOString().split('T')[0]}
                  value={formData.date}
                  onChange={handleChange}
                />
              </div>
              
              <div className="grid w-full items-center gap-2">
                <Label htmlFor="time">Preferred Time</Label>
                <Input
                  id="time"
                  name="time"
                  type="time"
                  required
                  value={formData.time}
                  onChange={handleChange}
                />
              </div>
            </div>
            
            <div className="grid w-full items-center gap-2">
              <Label htmlFor="reason">Reason for Visit</Label>
              <Textarea
                id="reason"
                name="reason"
                required
                placeholder="Please describe your symptoms or reason for the appointment"
                value={formData.reason}
                onChange={handleChange}
                className="resize-none"
                rows={3}
              />
            </div>
            
            <DialogFooter className="mt-6">
              <Button type="button" variant="outline" onClick={onClose} disabled={isSubmitting}>
                Cancel
              </Button>
              <Button 
                type="submit" 
                disabled={isSubmitting}
                className="bg-purple-800 hover:bg-purple-900"
              >
                {isSubmitting ? 'Booking...' : 'Book Appointment'}
              </Button>
            </DialogFooter>
          </form>
        )}
      </DialogContent>
    </Dialog>
  );
};

const DoctorCard = ({ doctor, onBook }) => {
  const [imageError, setImageError] = useState(false);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const isAvailable = isAvailableToday(doctor.availability);
  
  const placeholderUrl = `/api/placeholder/${300 + (doctor.id % 5) * 20}/${400 + (doctor.id % 3) * 20}`;
  
  const experienceText = doctor.experience === 1 
    ? "1 year experience" 
    : `${doctor.experience} years experience`;
    
  const appointmentCount = doctor.appointments ? doctor.appointments.length : 0;

  return (
    <div className="bg-white rounded-lg shadow-md overflow-hidden transition-transform duration-200 hover:shadow-lg hover:translate-y-1">
      <div className="relative">
        <img
          src={imageError ? placeholderUrl : doctor.image || placeholderUrl}
          alt={`Dr. ${doctor.name}`}
          className="w-full h-48 object-cover"
          onError={() => setImageError(true)}
        />
        {isAvailable && (
          <span className="absolute top-3 right-3 bg-green-500 text-white text-xs font-bold px-2 py-1 rounded-full flex items-center">
            <Clock className="w-3 h-3 mr-1" />
            Available Today
          </span>
        )}
        {doctor.experience > 15 && (
          <span className="absolute top-3 left-3 bg-blue-600 text-white text-xs font-bold px-2 py-1 rounded-full flex items-center">
            <Award className="w-3 h-3 mr-1" />
            Senior Expert
          </span>
        )}
      </div>
      
      <div className="p-5">
        <div className="flex justify-between items-start mb-2">
          <h2 className="text-xl font-bold text-gray-800">{doctor.name}</h2>
          <div className="flex items-center">
            <Star className="w-4 h-4 text-yellow-400 fill-current" />
            <span className="ml-1 text-sm font-medium">
              {(4 + ((doctor.id * 0.1) % 1)).toFixed(1)}
            </span>
          </div>
        </div>
        
        <span className="inline-block bg-blue-100 text-blue-800 text-xs px-2 py-1 rounded-full font-semibold mb-3">
          {doctor.specialization}
        </span>
        
        <div className="flex items-start mb-2">
          <MapPin className="w-4 h-4 text-gray-500 mt-1 flex-shrink-0" />
          <p className="text-gray-600 text-sm ml-2">{doctor.clinicAddress}</p>
        </div>
        
        <div className="flex items-center text-gray-500 text-sm mb-3">
          <Clock className="w-4 h-4 mr-1" />
          <span className="truncate">{getAvailabilityText(doctor.availability)}</span>
        </div>
        
        <div className="flex justify-between items-center mt-4">
          <div className="flex items-center space-x-1">
            <span className="text-gray-600 text-sm">{experienceText}</span>
            {appointmentCount > 0 && (
              <div className="flex items-center ml-3 text-sm text-gray-500">
                <Users className="w-4 h-4 mr-1" />
                <span>{appointmentCount}</span>
              </div>
            )}
          </div>
          <Button 
            className="bg-purple-800 hover:bg-purple-900" 
            onClick={() => setIsModalOpen(true)}
          >
            <Calendar className="w-4 h-4 mr-2" />
            Book Now
          </Button>
        </div>
      </div>

      {/* Pass the onBook callback to the BookingModal */}
      <BookingModal 
        isOpen={isModalOpen}
        onClose={() => setIsModalOpen(false)}
        doctor={doctor}
        onBook={onBook}
      />
    </div>
  );
};

const DoctorsPage = () => {
  // Store doctors in state to allow updates
  const [doctors, setDoctors] = useState(doctorsData.doctors);
  const [currentPage, setCurrentPage] = useState(1);
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedSpecialty, setSelectedSpecialty] = useState('All');
  const [todayOnly, setTodayOnly] = useState(false);
  const doctorsPerPage = 12; // Increase number of doctors per page if needed

  const specialties = ['All', ...new Set(doctors.map(doctor => doctor.specialization))];

  // Filter doctors based on search, specialty and availability
  const filteredDoctors = doctors.filter(doctor => {
    const matchesSearch = doctor.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         doctor.specialization.toLowerCase().includes(searchTerm.toLowerCase());
    const matchesSpecialty = selectedSpecialty === 'All' || doctor.specialization === selectedSpecialty;
    const matchesAvailability = !todayOnly || isAvailableToday(doctor.availability);
    return matchesSearch && matchesSpecialty && matchesAvailability;
  });

  const totalDoctors = filteredDoctors.length;
  const totalPages = Math.ceil(totalDoctors / doctorsPerPage);

  const indexOfLastDoctor = currentPage * doctorsPerPage;
  const indexOfFirstDoctor = indexOfLastDoctor - doctorsPerPage;
  const currentDoctors = filteredDoctors.slice(indexOfFirstDoctor, indexOfLastDoctor);

  useEffect(() => {
    setCurrentPage(1);
  }, [searchTerm, selectedSpecialty, todayOnly]);

  const nextPage = () => {
    if (currentPage < totalPages) setCurrentPage(currentPage + 1);
  };

  const prevPage = () => {
    if (currentPage > 1) setCurrentPage(currentPage - 1);
  };

  // Update doctor's appointments locally
  const handleBooking = (appointmentData) => {
    setDoctors(prevDoctors => prevDoctors.map(doc => {
      if (doc.id === appointmentData.doctorId) {
        return {
          ...doc,
          appointments: [...(doc.appointments || []), appointmentData]
        };
      }
      return doc;
    }));
  };

  return (
    <div className="bg-white min-h-screen">
      <div className="container max-w-[1500px] mx-auto px-4 py-8">
        <h1 className="text-3xl font-bold text-gray-800 mb-2">Find Your Doctor</h1>
        <p className="text-gray-600 mb-8">Book appointments with the best doctors in your area</p>
        
        {/* Search and Filter Section */}
        <div className="bg-white p-4 rounded-lg shadow-md mb-8">
          <div className="flex flex-col md:flex-row gap-4">
            <div className="relative flex-grow">
              <div className="absolute inset-y-0 left-0 flex items-center pl-3 pointer-events-none">
                <Search className="w-5 h-5 text-gray-400" />
              </div>
              <input
                type="text"
                placeholder="Search by name or specialty..."
                className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
              />
            </div>
            
            <div className="relative w-full md:w-64">
              <div className="absolute inset-y-0 left-0 flex items-center pl-3 pointer-events-none">
                <Filter className="w-5 h-5 text-gray-400" />
              </div>
              <select
                className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 appearance-none"
                value={selectedSpecialty}
                onChange={(e) => setSelectedSpecialty(e.target.value)}
              >
                {specialties.map((specialty) => (
                  <option key={specialty} value={specialty}>
                    {specialty}
                  </option>
                ))}
              </select>
            </div>
            
            <div className="flex items-center">
              <input
                type="checkbox"
                id="availableToday"
                className="mr-2 h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
                checked={todayOnly}
                onChange={() => setTodayOnly(!todayOnly)}
              />
              <label htmlFor="availableToday" className="text-sm text-gray-700">
                Available Today
              </label>
            </div>
          </div>
        </div>
        
        {/* Results Counter */}
        <div className="mb-4 text-gray-600">
          Showing {totalDoctors > 0 ? Math.min(totalDoctors, indexOfFirstDoctor + 1) : 0}-{Math.min(indexOfLastDoctor, totalDoctors)} of {totalDoctors} doctors
        </div>
        
        {/* Doctor Cards Grid */}
        {totalDoctors > 0 ? (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
            {currentDoctors.map((doctor) => (
              <DoctorCard key={doctor.id} doctor={doctor} onBook={handleBooking} />
            ))}
          </div>
        ) : (
          <div className="bg-white rounded-lg shadow-md p-8 text-center">
            <p className="text-gray-600 mb-2">No doctors match your search criteria</p>
            <button 
              className="text-blue-600 font-medium"
              onClick={() => {
                setSearchTerm('');
                setSelectedSpecialty('All');
                setTodayOnly(false);
              }}
            >
              Clear all filters
            </button>
          </div>
        )}
        
        {/* Pagination controls */}
        {totalDoctors > 0 && (
          <div className="flex justify-between items-center mt-8">
            <span className="text-sm text-gray-600">
              Page {currentPage} of {totalPages}
            </span>
            
            <div className="flex space-x-2">
              <button
                onClick={prevPage}
                disabled={currentPage === 1}
                className="flex items-center justify-center w-10 h-10 rounded-md border border-gray-300 bg-white text-gray-700 disabled:opacity-50 disabled:cursor-not-allowed"
              >
                <ChevronLeft className="w-5 h-5" />
              </button>
              
              {/* Page numbers */}
              <div className="hidden md:flex space-x-1">
                {[...Array(Math.min(5, totalPages))].map((_, i) => {
                  let pageNum;
                  if (totalPages <= 5) {
                    pageNum = i + 1;
                  } else if (currentPage <= 3) {
                    pageNum = i + 1;
                  } else if (currentPage >= totalPages - 2) {
                    pageNum = totalPages - 4 + i;
                  } else {
                    pageNum = currentPage - 2 + i;
                  }
                  
                  return (
                    <button
                      key={pageNum}
                      onClick={() => setCurrentPage(pageNum)}
                      className={`w-10 h-10 rounded-md ${
                        currentPage === pageNum
                          ? 'bg-purple-800 text-white'
                          : 'bg-white text-gray-700 border border-gray-300'
                      }`}
                    >
                      {pageNum}
                    </button>
                  );
                })}
              </div>
              
              <button
                onClick={nextPage}
                disabled={currentPage === totalPages}
                className="flex items-center justify-center w-10 h-10 rounded-md border border-gray-300 bg-white text-gray-700 disabled:opacity-50 disabled:cursor-not-allowed"
              >
                <ChevronRight className="w-5 h-5" />
              </button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default DoctorsPage;
